<template>
  <Teleport to="body">
    <div>
      <div @click="close" v-if="modelValue" id="defaultModal" tabindex="-1"
           class="fixed top-0 left-0 right-0 z-50 p-4 overflow-x-hidden w-full h-full md:h-full overflow-y-auto md:inset-0 h-modal justify-center items-center flex"
           aria-modal="true" role="dialog">
        <div :class="[sizeClasses]" @click.stop>
          <!-- Modal content -->
          <div class="relative bg-white rounded-lg shadow dark:bg-gray-700 flex flex-col max-h-[90vh]">
            <!-- Modal header -->
            <div class="flex items-start justify-between p-4 border-b rounded-t dark:border-gray-600">
              <h3 class="text-xl font-semibold text-gray-900 dark:text-white">
                <slot name="title">{{ title }}</slot>
              </h3>
              <button type="button"
                      @click="close"
                      class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
                      data-modal-hide="defaultModal">
                <svg aria-hidden="true" class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20"
                     xmlns="http://www.w3.org/2000/svg">
                  <path fill-rule="evenodd"
                        d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                        clip-rule="evenodd"></path>
                </svg>
                <span class="sr-only">Close modal</span>
              </button>
            </div>
            <!-- Modal body -->
            <div class="p-6 space-y-6 min-h-0 flex-1 scroll-auto max-h-100 overflow-auto">
              <div class="h-auto min-h-[200px]">
                <slot/>
              </div>
            </div>
            <!-- Modal footer -->
            <div v-if="buttons && buttons.length > 0"
                 class="flex items-center p-6 space-x-2 border-t border-gray-200 rounded-b dark:border-gray-600">
              <button data-modal-hide="defaultModal" type="button"
                      v-for="(btn, i) in buttons" :key="i"
                      :class="btn.cssClass"
                      @click="btn.click"
                      class="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">
                {{ btn.label }}
              </button>
            </div>
          </div>
        </div>
      </div>
      <div v-if="modelValue" modal-backdrop
           class="bg-gray-900 bg-opacity-50 dark:bg-opacity-80 fixed inset-0 z-40"></div>
    </div>
  </Teleport>
</template>

<script>
export default {
  name: "Popup",
  props: {
    modelValue: Boolean,
    title: String,
    size: {
      type: String,
      default: 'md'
    },
    buttons: Array // {label, cssClass, click}
  },
  computed: {
    sizeClasses() {
      const size = this.size
      if (size === 'sm') return 'relative w-full h-full max-w-md md:h-auto'
      if (size === 'lg') return 'relative w-full h-full max-w-4xl md:h-auto'
      if (size === 'xl') return 'relative w-full h-full max-w-7xl md:h-auto'
      // default md
      return 'relative w-full h-full max-w-lg md:h-auto'
    }
  },
  methods: {
    close() {
      this.$emit('update:modelValue', false)
    }
  }
}
</script>
